   "Quote": "“I don't want tea,\" said Clary, with muffled force. \"I want to find my mother. And then I want to find out who took her in the first place, and I want to kill them.\"\n\"Unfortunately,\" said Hodge, \"we're all out of bitter revenge at the moment, so it's either tea or nothing.”\n  \n  ―\n  \n    Cassandra Clare,\n  \n    \n      City of Bones",
