   "Quote": "“Whatever you are physically...male or female, strong or weak, ill or healthy--all those things matter less than what your heart contains. If you have the soul of a warrior, you are a warrior. All those other things, they are the glass that contains the lamp, but you are the light inside.”\n  \n  ―\n  \n    Cassandra Clare,\n  \n    \n      Clockwork Angel",
